//  –ú–æ—è –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω–∞—è –±–∏–±–ª–∏–æ—Ç–µ–∫–∞. –¢—É—Ç –Ω–∞—Ö–æ–¥—è—Ç—Å—è —Ñ—É–Ω–∫—Ü–∏–∏-–ø–æ–º–æ—â–Ω–∏–∫–∏, —á—Ç–æ–±—ã –Ω–µ –ø–æ–¥–∫–ª—é—á–∞—Ç—å JQuery.

/**
 * –§—É–Ω–∫—Ü–∏—è –∑–∞—Ö–≤–∞—Ç–∞ DOM-—ç–ª–µ–º–µ–Ω—Ç–∞
 * @param {string} selector –°–µ–ª–µ–∫—Ç–æ—Ä, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–µ—Ä–Ω—É—Ç—å —ç–ª–µ–º–µ–Ω—Ç
 * @return {object} –ü–µ—Ä–≤—ã–π –Ω–∞–π–¥–µ–Ω–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç DOM-–¥–µ—Ä–µ–≤–∞.
 */
function $(selector) {
	return document.querySelector(selector);
}

/**
 * –§—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –º–µ–Ω—è–µ—Ç (–ø–µ—Ä–µ–∫–ª—é—á–∞–µ—Ç) —É–∫–∞–∑–∞–Ω–Ω—ã–µ –∫–ª–∞—Å—Å—ã (–≤ ...rest -–ø–∞—Ä–∞–º–µ—Ç—Ä–µ) —É–∫–∞–∑–∞–Ω–Ω–æ–≥–æ DOM-—ç–ª–µ–º–µ–Ω—Ç–∞.
 * @description –° –ø–æ–º–æ—â—å—é CSS –∫–ª–∞—Å—Å–æ–≤ –Ω–µ–≤–æ–∑–º–æ–∂–Ω–æ –∏–∑–º–µ–Ω–∏—Ç—å —Å—Ç–∏–ª–∏ —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞. –î–ª—è —Ä–µ—à–µ–Ω–∏–∏ —ç—Ç–æ–≥–æ –ø—Ä–∏–º–µ–Ω—è–µ—Ç—Å—è –¥–∞–Ω–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è
 * @param {string} event –°–æ–±—ã—Ç–∏–µ, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É —Ñ—É–Ω–∫—Ü–∏—è –º–µ–Ω—è–µ—Ç –∫–ª–∞—Å—Å.
 * @param {object} eventElement –≠–ª–µ–º–µ–Ω—Ç, –Ω–∞ –∫–æ—Ç–æ—Ä–æ–º –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç —Å–æ–±—ã—Ç–∏–µ.
 * @param {object} targetElement –¶–µ–ª–µ–≤–æ–π —ç–ª–µ–º–µ–Ω—Ç, —á—å–∏ –∫–ª–∞—Å—Å—ã –¥–æ–ª–∂–Ω—ã –∏–∑–º–µ–Ω–∏—Ç—Å—è
 * @param {...any} classOpened –ö–ª–∞—Å—Å—ã, –∫–æ—Ç–æ—Ä—ã–µ –¥–æ–ª–∂–Ω—ã –ø–æ–º–µ–Ω—è—Ç—Å—è (rest-–ø–∞—Ä–∞–º–µ—Ç—Ä)
 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
 */
function changeClass(event, eventElement, targetElement, ...classOpened){
	eventElement.addEventListener(event, () => {
		for (let i = 0; i < classOpened.length; i++) {
			targetElement.classList.toggle(classOpened[i])
		}
	});
}

export {$, changeClass};