import { Book} from '../objectsClasses/books.class';
import { Genre } from '../objectsClasses/genres.class';
import { Author } from '../objectsClasses/authors.class';

/**
 * –ö–ª–∞—Å—Å –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞ "–§–æ—Ä–º–∞"
 */
export class Form{

	/**
	 * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –∫–ª–∞—Å—Å–∞ "Form". –°–æ–∑–¥–∞–µ—Ç –æ–±—ä–µ–∫—Ç -- –§–æ—Ä–º–∞, –∫–æ—Ç–æ—Ä—ã–π –ø–æ–¥–≤—è–∑—ã–≤–∞–µ—Ç—Å—è –ø–æ–¥ –ø–æ–¥ 1 HTML -—Ñ–æ—Ä–º—É.
	 * @param {object} currentForm  –¢–µ–∫—É—â–∞—è <HTML>-—Ñ–æ—Ä–º–∞, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–≤—è–∑—ã–≤–∞–µ—Ç—Å—è –∫ –¥–∞–Ω–Ω–æ–º—É –æ–±—ä–µ–∫—Ç—É
	 * @param {String} idName –Ω–∞–∑–≤–∞–Ω–∏–µ ID –¥–ª—è –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏ –∫–∞–∂–¥–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞
	 * @param {Array} dataArray (–Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π) –ú–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö, (–Ω–∞–±–æ—Ä –æ–±—ä–µ–∫—Ç–æ–≤, —á—Ç–æ –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤ —Ñ–æ—Ä–º–µ). –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –¥–∞–Ω–Ω—ã–µ –∑–∞–ø–∏—Å—ã–≤–∞—é—Ç—Å—è –≤  localStorage.
	 */
	constructor(currentForm, idName, dataArray){

		/**
		 * –¢–µ–∫—É—â–∞—è <HTML>-—Ñ–æ—Ä–º–∞, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–≤—è–∑—ã–≤–∞–µ—Ç—Å—è –∫ –¥–∞–Ω–Ω–æ–º—É –æ–±—ä–µ–∫—Ç—É
		 */
		this.currentForm = currentForm;

		/**
		 * –Ω–∞–∑–≤–∞–Ω–∏–µ ID –¥–ª—è –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏ –∫–∞–∂–¥–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞
		 */
		this.idName = idName;

		/**
		 * (–Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π) –ú–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö, (–Ω–∞–±–æ—Ä –æ–±—ä–µ–∫—Ç–æ–≤, —á—Ç–æ –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤ —Ñ–æ—Ä–º–µ). –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –¥–∞–Ω–Ω—ã–µ –∑–∞–ø–∏—Å—ã–≤–∞—é—Ç—Å—è –≤  localStorage.
		 */
		this.dataArray = [];

		/**	
		 * –ö–ª—é—á –æ—Ç localStorage, –≥–¥–µ —Ö—Ä–∞–Ω–∏—Ç—Å—è –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö —Å –æ–±—ä–µ–∫—Ç–∞–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –∫–æ—Ç–æ—Ä—ã–µ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –¥–∞–Ω–Ω–∞—è —Ñ–æ—Ä–º–∞.
		 */
		this.localStorageKey = this.getLocalStorageKey("-library");

		/**	
		 * –ù—É–∂–Ω–æ –ª–∏ –¥–æ–±–∞–≤–ª—è—Ç—å –¥–∞–Ω–Ω—ã–µ –≤ LocalStorege (true -–Ω—É–∂–Ω–æ, false -–Ω–µ –Ω—É–∂–Ω–æ)
		 */
		this.needAddToLocalStorege = true;

		// TODO –ö–∞–∫ —Å–¥–µ–ª–∞—Ç—å —Ç–∞–∫, —á—Ç–æ–±—ã –æ–±—ä–µ–∫—Ç—ã 2 —Ä–∞–∑–Ω—ã—Ö –∫–ª–∞—Å—Å–æ–≤ –∑–Ω–∞–ª–∏ –¥—Ä—É–≥ –æ –¥—Ä—É–≥–µ —Å—Ä–∞–∑—É, —á–µ—Ä–µ–∑ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä. (–ò –Ω—É–∂–Ω–æ –ª–∏ –≤–æ–æ–±—â–µ —ç—Ç–æ?)
		// this.tableObject = tableObject;
		// this.tableObject;

		if (dataArray) {
			this.dataArray = dataArray;
			this.needAddToLocalStorege = false;

		}else if(window.localStorage.getItem(this.localStorageKey)){
			this.dataArray = JSON.parse(window.localStorage.getItem(this.localStorageKey));
		}
		/**	
		 * –ü–æ—Å–ª–µ–¥–Ω–∏–π (—Ç–µ–∫—É—â–∏–π) –æ–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –∑–∞–ø–æ–ª–Ω—è–ª—Å—è.
		 */
		this.lastFilledObject = {}; 

		/**	
		 * –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –≤ —Ç–µ–∫—É—â–∏–π –º–æ–º–µ–Ω—Ç –≤—ã–±—Ä–∞–Ω –≤ —Ç–∞–±–ª–∏—Ü–µ.
		 */
		this.selectedObject;
	}

// --------------------------------------------------------------------------

	/**
	 * @param {object} –ü—Ä–∏–Ω–∏–º–∞–µ—Ç –æ–±—ä–µ–∫—Ç —Å 4 —Å–≤–æ–π—Å–≤–∞–º–∏ (—Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞ –∫–ª–∞—Å—Å–∞ Table) : currentTable - —Ç–µ–∫—É—â–∞—è —Ç–∞–±–ª–∏—Ü–∞, tableData - —Ç–µ–∫—É—â–∏–π –º–∞—Å—Å–∏–≤, –∫–æ—Ç–æ—Ä—ã–º –∑–∞–ø–æ–ª–Ω–µ–Ω–∞ —Ç–∞–±–ª–∏—Ü–∞, selectedObject - —Ç–µ–∫—É—â–∏–π –≤—ã–±—Ä–∞–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç –≤ —Ç–∞–±–ª–∏—Ü–µ
	 * –ú–µ—Ç–æ–¥ –ø–æ–¥–ø–∏—Å—á–∏–∫–∞, –æ–±–Ω–æ–≤–ª—è–µ—Ç –¥–∞–Ω–Ω—ã–µ "this.selectedObject"
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	observerUpdate({currentTable, tableData, selectedObject}){
		this.selectedObject = selectedObject
	}

// --------------------------------------------------------------------------
	/**
	 * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞ –≤ –±–∞–∑—É –¥–∞–Ω–Ω—ã—Ö. –ë–µ—Ä–µ—Ç —Å —Ñ–æ—Ä–º—ã –¥–∞–Ω–Ω—ã–µ –∏ –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç –≤ –±–∞–∑—É –¥–∞–Ω–Ω—ã—Ö.
	 * @param {string} dublicateKey –ö–ª—é—á, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥–µ—Ç –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç—å –ø–æ–∏—Å–∫ –¥—É–±–ª–∏–∫–∞—Ç–æ–≤ (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –¥—É–±–ª–∏–∫–∞—Ç–æ–º —Å—á–∏—Ç–∞–µ—Ç—Å—è –æ–±—ä–µ–∫—Ç —Å —Ç–∞–∫–∏–º-–∂–µ ID)
	 * @return {object} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç;
	 */
	addButtonHandler(dublicateKey = this.idName) {

		// –ü—Ä–æ–≤–µ—Ä–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏. –ï—Å–ª–∏ –≤–∞–ª–∏–¥–∞—Ü–∏—è –≤–µ—Ä–Ω—É–ª–∞ "false" ‚Äî —Ç–æ –∑–∞–∫–æ–Ω—á–∏—Ç—å –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ —Ç–µ–∫—É—â–µ–≥–æ –º–µ—Ç–æ–¥–∞
		if (this.validate() == false) {
			return false;
		}

		// –ó–∞–ø–æ–ª–Ω–µ–Ω–∏–µ —Å–æ–∑–¥–∞–Ω–Ω–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞
		this.fillObject();

		// –ü–æ–∏—Å–∫ –¥—É–±–ª–∏–∫–∞—Ç–æ–≤. –ï—Å–ª–∏ –Ω–∞—Ö–æ–¥–∏–º –¥—É–±–ª–∏–∫–∞—Ç –æ–±—ä–µ–∫—Ç–∞ - –º–µ—Ç–æ–¥ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç —Ä–∞–±–æ—Ç—É.
		if (this.findDublicate(dublicateKey)) { 
			return false;
		}
		
		// –û—á–∏—Å—Ç–∫–∞ –ø–æ–ª–µ–π –≤–≤–æ–¥–∞ —Ñ–æ—Ä–º—ã
		this.cleanInputs();
		
		// –î–æ–±–∞–≤–ª—è–µ–º –Ω–∞—Å—Ç—Ä–æ–µ–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç –≤ –±–∞–∑—É –¥–∞–Ω–Ω—ã—Ö.
		this.addToDatabase();

		//–í–æ–∑–≤—Ä–∞—â–∞–µ–º —Å–æ–∑–¥–∞–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç
		return this.lastFilledObject;
	}
	

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ —Å –ø–æ–ª–µ–π –≤–≤–æ–¥–∞ –≤ —Ñ–æ—Ä–º–µ (input)
	 * @param {number} id id –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è. (–ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –≥–µ–Ω–µ—Ä–∏—Ä—É–µ—Ç—Å—è –Ω–æ–≤—ã–π)
 	 * @return {object} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç
	 */
	fillObject(id){
		
		let elements = this.currentForm.elements, // –°–ø–∏—Å–æ–∫ —ç–ª–ª–µ–º–µ–Ω—Ç–æ–≤ —Ñ–æ—Ä–º—ã
			fillableObject, // –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω –∏ –≤–æ–∑–≤—Ä–∞—â–µ–Ω –º–µ—Ç–æ–¥–æ–º.
			accumulateValues = ""; // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è, –∫—É–¥–∞ –±—É–¥—É—Ç –Ω–∞–∫–∞–ø–ª–∏–≤–∞—Ç—å—Å—è –∑–Ω–∞—á–µ–Ω–∏—è –≤ —Å–ª—É—á–∞–µ –º–Ω–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω–æ–≥–æ –≤—ã–±–æ—Ä–∞ –∏–∑ <select>

		// –ü—Ä–æ–≤–µ—Ä–∫–∞ –∏ —Å–æ–∑–¥–∞–Ω–∏–µ –æ–±—ä–µ–∫—Ç–∞ –Ω—É–∂–Ω–æ–≥–æ —Ç–∏–ø–∞. 
		switch (this.getObjectType()) {

			case "book":
				this.lastFilledObject =  new Book;
				break;

			case "author":
				this.lastFilledObject =  new Author;
				break;

			case "genre":
				this.lastFilledObject =  new Genre;
				break;
		}
		// // –û—á–∏—â–∞–µ–º —Å–≤–æ–π—Å—Ç–≤–æ "this.lastFilledObject" –æ—Ç —Å—Ç–∞—Ä—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π. 
		// for (var prop in this.lastFilledObject) delete this.lastFilledObject[prop]; // ! –†–ê–ó–û–ë–†–ê–¢–¨–°–Ø –ò –£–î–ê–õ–ò–¢–¨ –ó–ê –ù–ï–ù–ê–î–û–ë–ù–û–°–¢–¨–Æ

		// –ï—Å–ª–∏ –º—ã –ø–µ—Ä–µ–¥–∞–ª–∏ "id" –∑–∞–ø–æ–ª–Ω—è–µ–º–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞ -- –∏—Å–ø–æ–ª—å–∑—É–µ–º –µ–≥–æ –≤ –∫–∞—á–µ—Å—Ç–≤–µ id, –µ—Å–ª–∏ –Ω–µ—Ç --–≥–µ–Ω–µ—Ä–∏—Ä—É–µ–º –Ω–æ–≤—ã–π id
		if (id) {
			this.lastFilledObject[this.idName] = parseInt(id);
		}else{
			// –°–æ–∑–¥–∞–µ–º –∏ –∑–∞–ø–æ–ª–Ω—è–µ–º ID –æ–±—ä–µ–∫—Ç–∞
			this.lastFilledObject[this.idName] = this.getID();
		}
		
		// –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤—Å–µ –ø–æ–ª—è –≤–≤–æ–¥–∞
		for (let i = 0; i < elements.length; i++) {
			// console.dir(elements[i]);
			// –ï—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã –Ω–µ –∏–º–µ–µ—Ç –∞—Ç—Ä–∏–±—É—Ç–∞ 'ignore'-- –∑–∞–ø–æ–ª–Ω—è–µ–º –æ–±—ä–µ–∫—Ç, –µ—Å–ª–∏ –∞—Ç—Ä–∏–±—É—Ç –∏–º–µ–µ—Ç—Å—è -- –∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞
			if (elements[i].hasAttribute('ignore') == false) {

				// –ï—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ç–∏–ø–∞ "select" –∏–º–µ–µ—Ç –∞—Ç—Ä–∏–±—É—Ç "multiple" (–ï—Å–ª–∏ –ø—Ä–∏—Å—É—Ç—Å—Ç–≤—É–µ—Ç –º–Ω–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω—ã–π –≤—ã–±–æ—Ä)
				if (elements[i].type == "select-multiple") {
					
					// –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤ –¥–∞–Ω–Ω–æ–º —ç–ª–µ–º–µ–Ω—Ç—ã –≤—Å–µ –≤—ã–±—Ä–∞–Ω–Ω—ã–µ <option>
					for (let k = 0; k < elements[i].selectedOptions.length; k++) {

						// –∏ –∫–æ–Ω–∫–∞—Ç–µ–Ω–∏—Ä—É–µ–º –∏—Ö –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é "accumulateValues"
						accumulateValues += `${elements[i].selectedOptions[k].value}, \n`;
					}
					// –ü–æ—Å–ª–µ —ç—Ç–æ–≥–æ —Å–æ–∑–¥–∞–µ–º —Å–≤–æ–π—Å—Ç–≤–æ –≤ –æ–±—ä–µ–∫—Ç–µ –∏ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º —Ä–µ–∑—É–ª—å—Ç–∞—Ç –∫–æ–Ω–∫–∞—Ç–µ–Ω–∞—Ü–∏–∏ –≤ —ç—Ç–æ —Å–≤–æ–π—Å—Ç–≤–æ.
					this.lastFilledObject[elements[i].getAttribute("id")] = accumulateValues;

				// –ò–Ω–∞—á–µ –ø—Ä–æ—Å—Ç–æ —Å–æ–∑–¥–∞–µ–º —Å–≤–æ–π—Å—Ç–≤–æ —É –æ–±—ä–µ–∫—Ç–∞ —Å —Ç–∞–∫–∏–º –∂–µ –∏–º–µ–Ω–µ–º, –∫–∞–∫ –∏ –∑–Ω–∞—á–µ–Ω–∏–µ –∞—Ç—Ä–∏–±—É—Ç–∞ "id" –≤ input
				}else{
					this.lastFilledObject[elements[i].getAttribute("id")] = elements[i].value;
				}
				
			} 
		}

		// –ö–ª–æ–Ω–∏—Ä—É–µ–º –æ–±—ä–µ–∫—Ç, —á—Ç–æ–±—ã –º–µ—Ç–æ–¥ —Å–º–æ–≥ –µ–≥–æ –≤–µ—Ä–Ω—É—Ç—å –∫–∞–∫ –æ—Ç–¥–µ–ª—å–Ω—ã–π –æ–±—ä–µ–∫—Ç, –∞ –Ω–µ —Å—Å—ã–ª–∫—É;
		fillableObject = Object.assign({},this.lastFilledObject)
		return fillableObject;
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –æ—á–∏—â–∞–µ—Ç –≤—Å–µ –ø–æ–ª—è —Ç–µ–∫—É—â–µ–π —Ñ–æ—Ä–º—ã.
	 * @todo –ü–µ—Ä–µ–¥–ª–µ–ª–∞—Ç—å —ç—Ç–æ—Ç –º–µ—Ç–æ–¥ —á–µ—Ä–µ–∑ —Ü–∏–∫–ª "forEach" –∏–ª–∏ "for in "
	 * @return –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */

	cleanInputs(){

		for (let i = 0; i < this.currentForm.elements.length; i++) {
			let boundId		 = this.currentForm.elements[i].dataset.boundId; // id —ç–ª–µ–º–µ–Ω—Ç–∞, –≤ –∫–æ—Ç–æ—Ä—ã–π –≤—ã–≤–æ–¥–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–∏
			let boundElement = document.querySelector(`#${boundId}`); // —ç–ª–µ–º–µ–Ω—Ç, –≤ –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –≤—ã–≤–æ–¥–∏—Ç—Å—è —Å–æ–æ–±—â–µ–Ω–∏–µ

			// –ï—Å–ª–∏ –ø–æ–ª–µ —Å–∫—Ä—ã—Ç–æ–µ, –∏–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã ‚Äî –∫–Ω–æ–ø–∫–∞, —Ç–æ –ø—Ä–æ–ø—É—Å–∫–∞–µ–º –∏ –Ω–µ –æ—á–∏—â–∞–µ–º —ç—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç—ã
			if (!(this.currentForm.elements[i].type == "hidden" || this.currentForm.elements[i].localName == "button" )) {
				this.currentForm.elements[i].value = "";
				this.currentForm.elements[i].classList.remove("inputs-invalid", "inputs-valid");
				this.currentForm.elements[i].classList.add("inputs-clean");
				if (boundElement) {
					boundElement.style.display = "none";
				}
			}
		}
		
		// –û—á–∏—â–∞–µ–º –ø–æ—Å–ª–µ–¥–Ω–∏–π –≤—ã–±—Ä–∞–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç;
		this.selectedObject = null;

		//~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~–í–∞—Ä–∏–∞–Ω—Ç1
		// for (const key in form.elements) {
		// 	if (form.elements.hasOwnProperty(key)) {
		// 		if (key.type != "hidden" || key.localName != "button") {
		// 			key.value = "";
		// 			key.className = "inputs-clean" 
		// 		}
		// 	}
		// }
		//~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~–í–∞—Ä–∏–∞–Ω—Ç2
		// form.elements.forEach(element => {
		// 	if (element.type != "hidden" || element.localName != "button") {
		// 		element.value = "";
		// 		element.className = "inputs-clean" 
		// 	}
		// });
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç, –æ–±—ä–µ–∫—Ç –∫–∞–∫–æ–≥–æ —Ç–∏–ø–∞ –∑–∞–ø–æ–ª–Ω—è—Ç—Å—è –≤ —Ñ–æ—Ä–º–µ. –î–ª—è —Ä–∞–±–æ—Ç—ã –º–µ—Ç–æ–¥–∞ –≤ —Ñ–æ—Ä–º–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø–æ–ª–µ <input type='hidden' id='objtype' value=''>, –∏–Ω–∞—á–µ –º–µ—Ç–æ–¥ –≤—ã–±—Ä–æ—Å–∏—Ç –∏—Å–∫–ª—é—á–µ–Ω–∏–µ.
	 * @return {string} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ç–∏–ø –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤ —Ç–µ–∫—É—â—É—é —Ñ–æ—Ä–º—É
	 */
	getObjectType(){
		for (let i = 0; i < this.currentForm.elements.length; i++) {
			if (this.currentForm.elements[i].id == "objtype") {
				return this.currentForm.elements[i].value
			}
		}
		throw "–ù–µ –Ω–∞–π–¥–µ–Ω–æ –ø–æ–ª–µ –≤–≤–æ–¥–∞  <input type='hidden'> —Å —É–∫–∞–∑–∞–Ω–∏–µ–º —Ç–∏–ø–∞ –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –ø–µ—Ä–µ–¥–∞–Ω –≤ –±–∞–∑—É –¥–∞–Ω–Ω—ã–π. –î–æ–±–∞–≤—å—Ç–µ –≤ –≤–∞—à—É —Ñ–æ—Ä–º—É –ø–æ–ª–µ <input type='hidden' id='objtype' value=''>  –í –ø–æ–ª–µ value='' —É–∫–∞–∂–∏—Ç–µ —Ç–∏–ø –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –¥–æ–±–∞–≤–ª–µ–Ω –≤ –±–∞–∑—É –¥–∞–Ω–Ω—ã—Ö"
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –¥—É–±–ª–∏–∫–∞—Ç—ã –æ–±—ä–µ–∫—Ç–æ–≤. –í —Å–ª—É—á–∞–µ –Ω–∞—Ö–æ–∂–¥–µ–Ω–∏—è –¥—É–±–ª–∏–∫–∞—Ç–∞ –≤—ã–≤–æ–¥–∏—Ç —Å–æ–æ–±—â–µ–Ω–∏–µ.
	 * @param {string} dublicateKey –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–æ–ª—è –æ–±—ä–µ–∫—Ç–∞, –ø–æ –∫–æ—Ç–æ—Ä–æ–±—É –±—É–¥–µ—Ç –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç—å –ø–æ–∏—Å–∫ –¥—É–±–ª–∏–∫–∞—Ç–æ–≤. 
	 * @return {boolean} true, –µ—Å–ª–∏ –¥—É–±–ª–∏–∫–∞—Ç –Ω–∞–π–¥–µ–Ω, false ‚Äî –µ—Å–ª–∏ –Ω–µ –Ω–∞—à–µ–ª.
	 */
	findDublicate(dublicateKey = this.idName){

		let flag;

		if (!(this.dataArray)) {
			return false;
		}
		// –¢—É—Ç –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç—Å—è —Å—Ä–∞–≤–Ω–µ–Ω–∏–µ –ø–æ –ø–æ–ª—é "dublicateKey". –ï—Å–ª–∏ –µ—Å—Ç—å —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ ‚Äî —Å—á–∏—Ç–∞–µ—Ç—Å—è, —á—Ç–æ –Ω–∞–π–¥–µ–Ω –¥—É–±–ª–∏–∫–∞—Ç –∏ –º–µ—Ç–æ–¥ –≤—ã–≤–æ–¥–∏—Ç —Å–æ–æ–±—â–µ–Ω–∏–µ.
		flag = this.dataArray.some(item =>item[dublicateKey] == this.lastFilledObject[dublicateKey]);
		if (flag) {
			alert ("—Ç–∞–∫–∞—è –∑–∞–ø–∏—Å—å —É–∂–µ –µ—Å—Ç—å");
		}
		return flag;
	}

// --------------------------------------------------------------------------

	//TODO –ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –º–µ—Ç–æ–¥ validate
	/**
	 * –ú–µ—Ç–æ–¥ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç —Ñ–æ—Ä–º—É –Ω–∞ –≤–∞–ª–∏–¥–Ω–æ—Å—Ç—å (—á—Ç–æ–±—ã –≤—Å–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ –ø–æ–ª—è –±—ã–ª–∏ –∑–∞–ø–æ–ª–Ω–µ–Ω—ã –º–∏–Ω–∏–º—É–º 1 —Å–∏–º–≤–æ–ª–æ–º). 
	 * @return {boolean} "true", –µ—Å–ª–∏ –≤—Å–µ –ø–æ–ª—è –≤–∞–ª–∏–¥–Ω—ã–µ. "false", –µ—Å–ª–∏ —Ö–æ—Ç—è-–±—ã 1 –ø–æ–ª–µ –Ω–µ –ø—Ä–æ—à–ª–æ –ø—Ä–æ–≤–µ—Ä–∫—É (—Ç–æ–≥–¥–∞ –∏ —Ñ–æ—Ä–º–∞ —Å—á–∏—Ç–∞–µ—Ç—Å—è –Ω–µ –≤–∞–ª–∏–¥–Ω–æ–π)
	 */
	validate() {
		
		let counterOfInvalid = 0, // –°—á–µ—Ç—á–∏–∫ –ø–æ–ª–µ–π, –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ –ø—Ä–æ—à–ª–∏ –≤–∞–ª–∏–¥–∞—Ü–∏—é
			elements = this.currentForm.elements; // –°–ø–∏—Å–æ–∫ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Ñ–æ—Ä–º—ã

		for (let i = 0; i < elements.length; i++) {

			let pattern 	 = elements[i].dataset.pattern, // —Ä–µ–≥—É–ª—è—Ä. –≤—ã—Ä–∞–∂., –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥–µ—Ç –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç—å –ø—Ä–æ–≤–µ—Ä–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏.
				message 	 = elements[i].dataset.message, // –°–æ–æ–±—â–µ–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ –≤—ã–≤–æ–¥–∏—Ç—Å—è –≤ —Å–ª—É—á–∞–µ –Ω–µ–≤–∞–ª–∏–¥–Ω–æ—Å—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç–∞
				boundId		 = elements[i].dataset.boundId, // id —ç–ª–µ–º–µ–Ω—Ç–∞, –≤ –∫–æ—Ç–æ—Ä—ã–π –≤—ã–≤–æ–¥–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–∏
				boundElement = document.querySelector(`#${boundId}`), // —ç–ª–µ–º–µ–Ω—Ç, –≤ –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –≤—ã–≤–æ–¥–∏—Ç—Å—è —Å–æ–æ–±—â–µ–Ω–∏–µ
				valid 		 = elements[i].value.search(pattern); // —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏–µ –≤–≤–µ–¥–µ–Ω–æ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è -- –ø–∞—Ç—Ç–µ—Ä–Ω—É

			// –í–∞–ª–∏–¥–∞—Ü–∏—é –±—É–¥–µ—Ç –ø—Ä–æ—Ö–æ–¥–∏—Ç—å —Ç–æ–ª—å–∫–æ —Ç–æ—Ç —ç–ª–µ–º–µ–Ω—Ç, —É –∫–æ—Ç–æ—Ä–æ–≥–æ –∏–º–µ–µ—Ç—Å—è —Å–≤–æ–π—Å—Ç–≤–æ "pattern"
			if (pattern) {

				if (valid == -1) {
					elements[i].classList.remove("inputs-valid");
					elements[i].classList.add("inputs-invalid");

					if (boundElement) boundElement.innerHTML = message;
					if (boundElement) boundElement.style.display = "block";
				
					counterOfInvalid++;
				}else{
					boundElement.style.display = "none";
					elements[i].classList.remove("inputs-invalid");
					elements[i].classList.add("inputs-valid");
				}
			}
		}

		// –ï—Å–ª–∏ —Å—á–µ—Ç—á–∏–∫ –Ω–µ–≤–∞–ª–∏–¥–Ω—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –±–æ–ª—å—à–µ –Ω—É–ª—è ‚Äî —Ç–æ –∏ —Å–∞–º–∞ —Ñ–æ—Ä–º–∞ –Ω–µ–≤–∞–ª–∏–¥–Ω–∞—è (–≤–µ—Ä–Ω—É—Ç—å "false")
		if (counterOfInvalid > 0) {
			return false;
		}else{
			return true;
		}
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –ø–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç Local Storage –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–º –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ –æ–±—ä–µ–∫—Ç–æ–º.
	 * @param {Array} newArray –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö, –∫–æ—Ç–æ—Ä—ã–º –Ω—É–∂–Ω–æ –ø–µ—Ä–µ–∑–∞–ø–∏—Å–∞—Ç—å Local Storage
	 * @return {Array} –í–æ–∑—Ä–∞—â–∞–µ—Ç –Ω–æ–≤—ã–π, –ø–µ—Ä–µ–∑–∞–ø–∏—Å–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ —Å Local Storage
	 */
	overwriteLocalStorage(newArray){

		if (this.needAddToLocalStorege) {
			localStorage.setItem(this.localStorageKey, JSON.stringify(newArray));
			return JSON.parse(localStorage.getItem(this.localStorageKey))
		}else{ // –°–ª—É—á–∞–π, –µ—Å–ª–∏ –º—ã –Ω–µ —Ä–∞–±–æ—Ç–∞–µ–º —Å localStorage, –Ω–æ –º–µ—Ç–æ–¥ –±—ã–ª –≤—ã–∑–≤–∞–Ω.
			console.warn(`–°–≤–æ–π—Å—Ç–≤–æ ${this}.needAddToLocalStorege –≤ –∑–Ω–∞—á–µ–Ω–∏–∏ ${this.needAddToLocalStorege}. –ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–ø–∏—Å–∞—Ç—å –≤ Local Storage`); 
			return newArray;
		}
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥  –¥–æ–±–∞–≤–ª—è–µ—Ç –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ –æ–±—ä–µ–∫—Ç -- –≤ Local Storage.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	addToDatabase(object = this.lastFilledObject) {

		// –î–æ–±–∞–≤–ª—è–µ–º –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç "object" –≤ –º–∞—Å–∏–∏–≤ –¥–∞–Ω–Ω—ã—Ö "this.dataArray"
		this.dataArray.push(object);

		// –ü–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞–µ–º LocalStorage
		return this.overwriteLocalStorage(this.dataArray);
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π —É–¥–∞–ª—è–µ—Ç –æ–±—ä–µ–∫—Ç –∏–∑ –±–∞–∑—ã –¥–∞–Ω–Ω—ã—Ö, –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª –ø–µ—Ä–µ–¥–∞–Ω –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ.
	 * @param {object} object –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —É–¥–∞–ª–∏—Ç—å. –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é "this.selectedObject"
	 * @return {Array} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∞–∫—Ç—É–∞–ª—å–Ω—ã–π –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö. (–∏–ª–∏ false, –µ—Å–ª–∏ –Ω–µ—Ç –æ–±—ä–µ–∫—Ç–∞ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è)
	 */
	deleteObject(object = this.selectedObject){
		
		if (object) {
			this.dataArray.splice(this.findObject(object), 1);
			
			// –û—á–∏—â–∞–µ–º –ø–æ–ª—è –≤–≤–æ–¥–∞.
			this.cleanInputs();
			this.selectedObject = null;
			return this.overwriteLocalStorage(this.dataArray);
		}else{
			return false;
		}

	}
	
// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –∏–∑–º–µ–Ω—è–µ—Ç –æ–±—ä–µ–∫—Ç –≤ –±–∞–∑–µ –¥–∞–Ω–Ω—ã—Ö, –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª –ø–µ—Ä–µ–¥–∞–Ω –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ.
	 * @param {object} object –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∏–∑–º–µ–Ω–∏—Ç—å. –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é "this.selectedObject"
	 * @return {Array} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∞–∫—Ç—É–∞–ª—å–Ω—ã–π –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö. 
	 */
	editObject(object = this.selectedObject){

		let copiedObject; // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è, —á—Ç–æ–±—ã —Å–∫–ª–æ–Ω–∏—Ä–æ–≤–∞—Ç—å –æ–±—ä–µ–∫—Ç "this.lastFilledObject"
		
		if (object && this.validate()) {
			this.fillObject(object[this.idName])

			// –ö–ª–æ–Ω–∏—Ä—É–µ–º –æ–±—ä–µ–∫—Ç "this.lastFilledObject", –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –∏–∑–±–∞–≤–∏—Ç—å—Å—è –æ—Ç –∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∏—è –ø–æ —Å—Å—ã–ª–∫–µ.
			copiedObject = Object.assign({},this.lastFilledObject)

			this.dataArray[this.findObject(object)] = copiedObject; 
			return this.overwriteLocalStorage(this.dataArray);
		} else {
			return false;
		}

	}
	
// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –Ω–∞—Ö–æ–¥–∏—Ç –∏–Ω–¥–µ–∫—Å –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ –æ–±—ä–µ–∫—Ç–∞ –≤ —Ç–µ–∫—É—â–µ–º –º–∞—Å—Å–∏–≤–µ –¥–∞–Ω–Ω—ã—Ö (dataArray)
	 * @param {object} object –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –Ω—É–∂–Ω–æ –Ω–∞–π—Ç–∏ –≤ –º–∞—Å—Å–∏–≤–µ –¥–∞–Ω–Ω—ã—Ö.
	 * @return {number} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∏–Ω–¥–µ–∫—Å –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª –ø–µ—Ä–µ–¥–∞–Ω –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä.
	 */
	findObject(object){

		return  this.dataArray.findIndex((item)=>{
			// –ï—Å–ª–∏ —Å–æ–≤–ø–∞–¥–∞—é—Ç –∑–Ω–∞—á–µ–Ω–∏—è "id" –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º –æ–±—ä–µ–∫—Ç–µ –∏ –≤ –º–∞—Å—Å–∏–≤–µ –¥–∞–Ω–Ω—ã—Ö -- –≤–æ–∑—Ä–∞—â–∞–µ–º –∏–Ω–¥–µ–∫—Å. 
			return item[this.idName] == object[this.idName]
		})
	}

// --------------------------------------------------------------------------
	/**
	 *  –ú–µ—Ç–æ–¥ —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç —É–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –¥–ª—è –Ω–æ–≤–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞.
	 * @return {number} –í–æ–∑—Ä–∞—â–∞–µ—Ç –Ω–æ–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ "id", –∫–æ—Ç–æ—Ä–æ–≥–æ –µ—â–µ –Ω–µ –±—ã–ª–æ (—Å–∞–º–æ–µ –±–æ–ª—å—à–æ–µ —Ç–µ–∫—É—â–µ–µ "id" +1)
	 */
	getID() {

		let currentId = 0; // –¢–µ–∫—É—â–∏–π "id"

		// –ï—Å–ª–∏ —É –Ω–∞—Å –ø—Ä–∏—Å—É–¥—Å—Ç–≤—É–µ—Ç –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏, —Ç–æ...
		if (this.dataArray) {
			// ...–∏—â–µ–º —Å–∞–º–æ–µ –±–æ–ª—å—à–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ "id"
			this.dataArray.forEach(element => {
				
				if( parseInt(element[this.idName])  > currentId){
					currentId  = parseInt(element[this.idName])
				}
			});
			// –ò –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –Ω–∞ 1 –±–æ–ª—å—à–µ
			return currentId + 1;

		// –ï—Å–ª–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–µ—Ç -- –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –ø—Ä–æ—Å—Ç–æ 1 (–ø—Ä–µ–¥–ø–æ–ª–æ–≥–∞–µ—Ç—Å—è —á—Ç–æ —ç—Ç–æ –∑–∞–ø—Ä–æ—Å –ø–µ—Ä–µ–¥ —Å–æ–∑–¥–∞–Ω–∏–µ–º –º–∞—Å—Å–∏–≤–∞ —Å –¥–∞–Ω–Ω—ã–º–∏)
		}else{
			return 1;
		}
	}

// --------------------------------------------------------------------------

	/**
	 * –ú–µ—Ç–æ–¥ —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç –∫–ª—é—á –¥–ª—è LocalStorage.
	 * @param {string} postfix –ü–æ—Å—Ç—Ñ–∏–∫—Å (–ß–∞—Å—Ç—å, –∫–æ—Ç–æ—Ä–∞—è —É–∫–∞–∑—ã–≤–∞–µ—Ç—Å—è –≤ –Ω–∞–∑–≤–∞–Ω–∏–∏ –∫–ª—é—á–∞ –ø–æ—Å–ª–µ —Ç–∏–ø–∞ –æ–±—ä–µ–∫—Ç–∞ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –∏–º–µ–Ω–∏ –∫–ª—é—á–∞ LocalStorage (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é "-library"))
	 * @return {string} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å–æ–∑–¥–∞–Ω–Ω—ã–π –∫–ª—é—á –¥–ª—è LocalStorage.
	 */
	getLocalStorageKey(postfix = "-library"){

		//–ù–∞—Ö–æ–¥–∏–º –≤ —Ñ–æ—Ä–º–µ –ø–æ–ª–µ –≤–≤–æ–¥–∞ —Å id == "objtype" (—ç—Ç–æ –ø–æ–ª–µ —Ç–∏–ø–∞  "hidden")
		for (let i = 0; i < this.currentForm.elements.length; i++) {
			if (this.currentForm.elements[i].id == "objtype") {
				
				// –í–æ–∑–≤—Ä–∞—â–∞–µ–º —Å–æ–∑–¥–∞–Ω–Ω—ã–π  –∫–ª—é—á
				return this.currentForm.elements[i].value + postfix;
			}
		}

		throw "–ú–µ—Ç–æ–¥ –Ω–µ —Å–º–æ–≥ –Ω–∞–π—Ç–∏ –ø–æ–ª–µ —Å id == 'objtype' –∏ value == '<–Ω–∞–∑–≤–∞–Ω–∏–µ –≤–∞—à–µ–≥–æ –æ–±—ä–µ–∫—Ç–∞>'. –£–±–µ–¥–∏—Ç–µ—Å—å —á—Ç–æ –æ–Ω–æ –µ—Å—Ç—å "
	}

// --------------------------------------------------------------------------

	/**
	 * –ú–µ—Ç–æ–¥ –Ω–∞—Ö–æ–¥–∏—Ç –ø–æ–ª–µ –¥–ª—è –≤–≤–æ–¥–∞, —É –∫–æ—Ç–æ—Ä–æ–≥–æ –µ—Å—Ç—å –ø—Ä–∏–≤—è–∑–∫–∞ –∫ –¥–∞–Ω–Ω—ã–º –≤ –±–∞–∑–µ –¥–∞–Ω–Ω—ã—Ö –∏ –∑–∞–ø–æ–ª–Ω—è–µ—Ç –¥–∞–Ω–Ω—ã–º–∏.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	checkForm(){

		let localStorageKeyBind,
			objectPropertyBind ,
			arrayOfObjects;

		// –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã —Ñ–æ—Ä–º—ã
		for (let i = 0; i < this.currentForm.elements.length; i++) {

			localStorageKeyBind = this.currentForm.elements[i].dataset.localStorageKeyBind;
			objectPropertyBind = this.currentForm.elements[i].dataset.objectPropertyBind;
			arrayOfObjects = JSON.parse(window.localStorage.getItem(localStorageKeyBind));

			// –ï—Å–ª–∏ –ø–µ—Ä–µ–±–∏—Ä–∞–µ–º–æ–µ –ø–æ–ª–µ –¥–ª—è –≤–≤–æ–¥–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç –∫–∞–∫—É—é -–ª–∏–±–æ –∏–∑ –ø—Ä–∏–≤—è–∑–æ–∫ -- —Ç–æ–≥–¥–∞ –∑–∞–ø–æ–ª–Ω—è–µ–º —ç—Ç–æ –ø–æ–ª–µ –æ–±—ä–µ–∫—Ç–∞–º–∏ 
			if (localStorageKeyBind && objectPropertyBind) {
				this.fillInput(this.currentForm.elements[i], arrayOfObjects);

			// –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –ø—Ä–∏–≤—è–∑–∞–ª —á—Ç–æ-—Ç–æ –æ–¥–Ω–æ (–∏–∑ –¥–≤—É—Ö –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã—Ö –∞—Ç—Ä–∏–±—É—Ç–æ–≤) -- –≤—ã–±—Ä–∞—Å—ã–≤–∞–µ–º –∏—Å–∫–ª—é—á–µ–Ω–∏–µ
			} else if(localStorageKeyBind){
				throw "–ù–µ—Ç –ø—Ä–∏–≤—è–∑–∫–∏ –∫ —Å–≤–æ–π—Å—Ç–≤—É –æ–±—ä–µ–∫—Ç–∞. –£–∫–∞–∂–∏—Ç–µ –≤ HTML —ç–ª–µ–º–µ–Ω—Ç–µ –∞—Ç—Ä–∏–±—É—Ç <objectPropertyBind> —Å–æ –∑–Ω–∞—á–µ–Ω–∏–µ–º —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞, –∫–æ—Ç–æ—Ä–æ–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤—ã–≤–æ–¥–∏—Ç—å."
			} else if(objectPropertyBind){
				throw "–ù–µ—Ç –ø—Ä–∏–≤—è–∑–∫–∏ –∫ –ú–∞—Å—Å–∏–≤—É –æ–±—ä–µ–∫—Ç–æ–≤ –≤ LocalStorage. –£–∫–∞–∂–∏—Ç–µ –∞—Ç—Ä–∏–±—É—Ç <localStorageKeyBind> —Å–æ –∑–Ω–∞—á–µ–Ω–∏–µ–º –∫–ª—é—á–∞, –∏–∑ –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –¥–ª—è –ø—Ä–∏–≤—è–∑–∫–∏."
			}
		}
	}

// --------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –∑–∞–ø–æ–ª–Ω—è–µ—Ç —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ —Å –º–∞—Å—Å–∏–≤–∞.
	 * @param {object} element –ï–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã, –∫–æ—Ç–æ—Ä—ã–π –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å
	 * @param {Array} arrayOfObjects –ú–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤, –∏–∑ –∫–æ—Ç–æ—Ä—ã—Ö –Ω—É–∂–Ω–æ –≤–∑—è—Ç—å –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
	 * @return {object} –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç
	 */
	fillInput(element, arrayOfObjects){

		let bindingKey = element.dataset.objectPropertyBind; // –°–≤–æ–π—Å—Ç–≤–æ –æ–±—ä–µ–∫—Ç–∞, –∏–∑ –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–∑—è—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –∏ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –≤ —ç–ª–µ–º–µ–Ω—Ç.
		let elementType = element.tagName.toLowerCase(); // –¢–∏–ø  DOM-—ç–ª–µ–º–µ–Ω—Ç–∞

		if (arrayOfObjects) {
			switch (elementType) {

				// –ø–æ–∫–∞ –º–µ—Ç–æ–¥ —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω —Ç–æ–ª—å–∫–æ —á—Ç–æ–±—ã –∑–∞–ø–æ–ª–Ω—è—Ç—å <select> 
				case "select":
					for (let i = 0; i < arrayOfObjects.length; i++) {
						let optionElement = document.createElement("option");

						optionElement.value = arrayOfObjects[i][bindingKey];
						optionElement.innerHTML = arrayOfObjects[i][bindingKey];
						element.appendChild(optionElement);
					}
					return element;
				default:
					break;
			}
		}else{
			return false;
		}	
	}

	/**
	 * –ú–µ—Ç–æ–¥ –∑–∞–ø–æ–ª–Ω—è–µ—Ç –ø–æ–ª—è —Ç–µ–∫—É—â–µ–π —Ñ–æ—Ä–º—ã (–≤—Å–µ–π —Ñ–æ—Ä–º—ã) –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ –∏–∑ –æ–±—ä–µ–∫—Ç–∞
	 * @param {object} fillingObject –û–±—ä–µ–∫—Ç –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Ñ–æ—Ä–º—ã. 
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	fillForm(fillingObject){

		// –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã —Ñ–æ—Ä–º—ã
		for (let i = 0; i < this.currentForm.elements.length; i++) {

			// –ï—Å–ª–∏ –ø–æ–ª–µ –Ω–µ —Å–∫—Ä—ã—Ç–æ–µ, –∏–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã ‚Äî –Ω–µ –∫–Ω–æ–ø–∫–∞
			if (!(this.currentForm.elements[i].type == "hidden" || this.currentForm.elements[i].localName == "button" )) {

				// –ø–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤—Å–µ —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞ "fillingObject"
				for (const key in fillingObject) {

					// –ï—Å–ª–∏ –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞ —Å–æ–æ—Ç–≤–µ—Ç—Å–≤—É–µ—Ç –∑–Ω–∞—á–µ–Ω–∏—é "id" –≤ input-—Ç–µ
					if (key == this.currentForm.elements[i].id){

						// —Ç–æ–≥–¥–∞ –∑–∞–ø–æ–ª–Ω—è–µ–º –ø–æ–ª–µ –∑–Ω–∞—á–µ–Ω–∏–µ–º —Å–æ —Å–≤–æ–π—Å–≤–∞ –æ–±—ä–µ–∫—Ç–∞.
						this.currentForm.elements[i].value = fillingObject[key];
					}
				}
			}
		}
	}
}