import { $ } from "../myHelperLib";

/**
 * –ö–ª–∞—Å—Å –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞ "–¢–∞–±–ª–∏—Ü–∞"
 * –û–±—ä–µ–∫—Ç —Ç–∞–±–ª–∏—Ü–∞ —è–≤–ª—è–µ—Ç—Å—è –ù–∞–±–ª—é–¥–∞–µ–º—ã–º –æ–±—ä–µ–∫—Ç–æ–º (Observable) 
 */
export class Table{

	/**
	 * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä. –°–æ–∑–¥–∞–µ—Ç –æ–±—ä–µ–∫—Ç -- —Ç–∞–±–ª–∏—Ü–∞, –∫–æ—Ç–æ—Ä–∞—è –ø–æ–¥–≤—è–∑—ã–≤–∞–µ—Ç—Å—è –ø–æ–¥ 1 HTML-—Ç–∞–±–ª–∏—Ü—É
	 * @param {String} currentTable CSS -—Å–µ–ª–µ–∫—Ç–æ—Ä –¥–ª—è –Ω–∞—Ö–æ–∂–¥–µ–Ω–∏—è –Ω—É–∂–Ω–æ–π (—Å–≤—è–∑–∞–Ω–Ω–æ–π —Ç–∞–±–ª–∏—Ü—ã)
	 * @param {object} formObject –û–±—ä–µ–∫—Ç —Ñ–æ—Ä–º—ã, —Å–æ–∑–¥–∞–Ω–Ω—ã–π –∫–ª–∞—Å—Å–æ–º "Form"
	 * @param {Array} tableData -- –ú–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ã–º–∏ –±—É–¥–µ—Ç –∑–∞–ø–æ–ª–Ω—è—Ç—Å—è —Ç–∞–±–ª–∏—Ü–∞
	 */
	constructor(currentTable,formObject,tableData){

		/**
		 * –¢–µ–∫—É—â–∞—è (—Å–≤—è–∑–∞–Ω–Ω–∞—è DOM-—Ç–∞–±–ª–∏—Ü–∞).
		 */
		this.currentTable = $(currentTable);

		/**
		 * –¢–µ–∫—É—â–∏–π –æ–±—ä–µ–∫—Ç —Ñ–æ—Ä–º—ã(—Å–≤—è–∑–∞–Ω–Ω–∞—è —Ñ–æ—Ä–º–∞).
		 */
		this.formObject = formObject;

		/**
		 * –î–∞–Ω–Ω—ã–µ, –∫–æ—Ç–æ—Ä—ã–µ –∑–∞–ø–æ–ª–Ω—è—é—Ç—Å—è –≤ —Ç–∞–±–ª–∏—Ü—É.
		 */
		this.tableData = tableData;

		/**	
		 * –ü—Ä–∏–≤—è–∑–∞–Ω–Ω–∞—è —Ñ–æ—Ä–º–∞ –ø–æ–¥ –¥–∞–Ω–Ω—É—é —Ç–∞–±–ª–∏—Ü—É. –û–±—ä–µ–∫—Ç –∫–ª–∞—Å—Å–∞ "Form".
		 * @type {object}
		 */
		this.boundForm = formObject.currentForm;

		/**
		 * –ö–ª—é—á –æ—Ç localStorage, –≥–¥–µ —Ö—Ä–∞–Ω–∏—Ç—Å—è –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö —Å –æ–±—ä–µ–∫—Ç–∞–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª–µ–Ω—ã –≤ –¥–∞–Ω–Ω–æ–π —Ç–∞–±–ª–∏—Ü–µ
		 * @type {string}
		 */
		this.localStorageKey = formObject.localStorageKey

		/**	
		 * –°–ø–∏—Å–æ–∫ –ø–æ–¥–ø–∏—Å—á–∏–∫–æ–≤ (–ø–∞—Ç—Ç–µ—Ä–Ω Observer)
		 * @type {Array}
		 */
		this.arrayOfObservers = [];

		/**
		 * –°–≤–æ–π—Å—Ç–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –∑–Ω–∞—á–µ–Ω–∏–µ, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥—É—Ç –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã –¥–∞–Ω–Ω—ã–µ –≤ —Ç–∞–±–ª–∏—Ü–µ
		 * @type {string}
		 */
		this.sortMark;

		/**
		 * –°–≤–æ–π—Å—Ç–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –¥–∞–Ω–Ω—ã—Ö –≤ —Ç–∞–±–ª–∏—Ü–µ. (–ø—Ä—è–º–æ–µ –∏–ª–∏ –æ–±—Ä–∞—Ç–Ω–æ–µ)
		 * @type {boolean}
		 */
		this.sortDirection = true;

		/**	
		 * –°–≤–æ–π—Å—Ç–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π (–ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å—Ç—Ä–æ–∫—É —Ç–∞–±–ª–∏—Ü—ã)
		 * @type {function}
		 */
		this.otherRowSelectHandler;

		/**
		 * –ò–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏, –∫–æ—Ç–æ—Ä–∞—è –≤ –¥–∞–Ω–Ω—ã–π –º–æ–º–µ–Ω—Ç –≤—ã–¥–µ–ª–µ–Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –≤ —Ç–∞–±–ª–∏—Ü–µ. (DOM-—ç–ª–µ–º–µ–Ω—Ç)
		 * @type {number}
		 */
		this.selectedRowIndex = null;

		/**
		 * –û–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–≤—è–∑–∞–Ω –∫ —Ç–µ–∫—É—â–µ–π –≤—ã–¥–µ–ª–µ–Ω–Ω–æ–π —Å—Ç—Ä–æ–∫—É —Ç–∞–±–ª–∏—Ü—ã —Ç–∞–±–ª–∏—Ü–µ
		 * @type {object}
		 */
		this.selectedObject;

	}

	/**
	 * –ú–µ—Ç–æ–¥ –¥–æ–±–∞–≤–ª—è–µ—Ç –ø–æ–¥–ø–∏—Å—á–∏–∫–∞ –≤ –º–∞—Å—Å–∏–≤ –ø–æ–¥–ø–∏—Å—á–∏–∫–æ–≤ (–ø–∞—Ç—Ç–µ—Ä–Ω Observer)
	 * @param {object} observers (rest-–ø–∞—Ä–∞–º–µ—Ç—Ä) –ü–æ–¥–ø–∏—Å—á–∏–∫–∏ (–Ω–∞–±–ª—é–¥–∞—Ç–µ–ª–∏) –∑–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è–º–∏
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç.
	 */
	addObservers(...observers){
		observers.forEach(element => {
			this.arrayOfObservers.push(element);
		});
	}

	/**
	 * –ú–µ—Ç–æ–¥ - —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –ø–æ–¥–ø–∏—Å—á–∏–∫–æ–≤ –æ–± –∏–∑–º–µ–Ω–µ–Ω–∏—è—Ö (–ø–∞—Ç—Ç–µ—Ä–Ω Observer). –≠—Ç–æ—Ç –º–µ—Ç–æ–¥ —Å–æ–∑–¥–∞–≤–∞–ª—è –¥–ª—è —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü–∏–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ "tableData" –∏ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ –≤—ã–±–æ—Ä–∞ —Å—Ç—Ä–æ–∫–∏ "rowSelectHandler". –ó–¥–µ—Å—å –æ–±—ä–µ–∫—Ç –∫–ª–∞—Å–∞ Table –û–î–ù–û–í–†–ï–ú–ï–ù–ù–û –∏ –ò–ó–î–ê–¢–ï–õ–¨ –∏ –ü–û–î–ü–ò–°–ß–ò–ö.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç.
	 */
	notify(){
		this.arrayOfObservers.forEach(element => {
			element.observerUpdate({currentTable: this.currentTable, tableData: this.tableData, selectedObject: this.selectedObject})
		});
		// console.log(`selectedObject = `, this.selectedObject);
	}
	
// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ update –ø–∞—Ç—Ç–µ—Ä–Ω–∞ Observer
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç.
	 */
	observerUpdate(){
		if (this.otherRowSelectHandler) {
			this.currentTable.onclick = this.otherRowSelectHandler.bind(this);
		}else{
			this.currentTable.onclick = this.rowSelectHandler.bind(this);
		}
	}

// ---------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –∑–∞–ø–æ–ª–Ω—è–µ—Ç —É–∫–∞–∑–∞–Ω–Ω—É—é —Ç–∞–±–ª–∏—Ü—É, –≤–∑—è–≤ –¥–∞–Ω–Ω—ã–µ —Å LocalStorage –ø–æ –∫–ª—é—á—É, –ª–∏–±–æ —É–∫–∞–∑–∞–Ω–Ω—ã–º —Ä–∞–Ω–µ–µ –º–∞—Å—Å–∏–≤–æ–º –æ–±—ä–µ–∫—Ç–æ–≤.
	 * @return {void} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏, –∫–æ—Ç–æ—Ä—ã–º–∏ –±—ã–ª–∞ –∑–∞–ø–æ–ª–Ω–µ–Ω–∞ —Ç–∞–±–ª–∏—Ü–∞.
	 */
	fillTable(sortMark,sortDirection) {
	
		if (sortMark) {
			this.sortMark = sortMark;
		}
		if (sortDirection) {
			this.sortDirection = sortDirection;
		}
		
		// –ï—Å–ª–∏ —É –Ω–∞—Å –µ—Å—Ç—å —Å–≤—è–∑–∞–Ω–Ω–∞—è —Ç–∞–±–ª–∏—Ü–∞ –∏ –≥–æ—Ç–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Ç–∞–±–ª–∏—Ü—ã
		if (this.currentTable && this.tableData) {
			
			// –ó–∞–ø–æ–ª–Ω—è–µ–º —Ç–∞–±–ª–∏—Ü—É –¥–∞–Ω–Ω—ã–º–∏
			for (let i = 0; i < this.tableData.length; i++) { // ? ForEach? 
				this.addRows(this.tableData[i]).rowIndex; 
			}

			//–í–æ–∑–≤—Ä–∞—â–∞–µ–º –º–∞—Å—Å–∏–≤, –∫–æ—Ç–æ—Ä—ã–º –∑–∞–ø–æ–ª–Ω–∏–ª–∏ —Ç–∞–±–ª–∏—Ü—É
			return this.tableData;

		// –ï—Å–ª–∏ —É –Ω–∞—Å –µ—Å—Ç—å —Å–≤—è–∑–∞–Ω–Ω–∞—è —Ç–∞–±–ª–∏—Ü–∞ –∏ –∫–ª—é—á –æ—Ç localStorage
		}else if (this.currentTable && this.localStorageKey) {

			// –î–æ—Å—Ç–∞–µ–º –¥–∞–Ω–Ω—ã–µ –∏–∑ localStorage
			this.tableData = this.getTableData();

			// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –¥–∞–Ω–Ω—ã—Ö –≤ localStorage //TODO –ü—Ä–æ–≤–µ—Ä–∏—Ç—å, –ø—Ä–∞–≤–∏–ª—å–Ω–∞—è –ª–∏ –ø—Ä–æ–≤–µ—Ä–∫–∞
			if (this.tableData == false) {

				// –ï—Å–ª–∏ –≤ localStorage –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤ -- –≤–æ–∑–≤—Ä–∞—â–∞–µ–º false
				return false; 
			}

			// –ó–∞–ø–æ–ª–Ω—è–µ–º —Ç–∞–±–ª–∏—Ü—É –¥–∞–Ω–Ω—ã–º–∏
			for (let i = 0; i < this.tableData.length; i++) { // ? ForEach? 
				this.addRows(this.tableData[i]).rowIndex; 
			}

			//–í–æ–∑–≤—Ä–∞—â–∞–µ–º –º–∞—Å—Å–∏–≤, –∫–æ—Ç–æ—Ä—ã–º –∑–∞–ø–æ–ª–Ω–∏–ª–∏ —Ç–∞–±–ª–∏—Ü—É
			return this.tableData;

		}else{

			// –ï—Å–ª–∏ —Ç–∞–±–ª–∏—Ü–∞ –Ω–µ –ø–æ–¥–≤—è–∑–∞–Ω–∞ -- –≤—ã–±—Ä–æ—Å –∏—Å–∫–ª—é—á–µ–Ω–∏—è
			if (this.currentTable == false) {
				throw `–ù–µ –Ω–∞–π–¥–µ–Ω–∞ —Å–≤—è–∑–∞–Ω–Ω–∞—è HTML-—Ç–∞–±–ª–∏—Ü–∞. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –¥–æ–±–∞–≤—å—Ç–µ HTML-—Ç–∞–±–ª–∏—Ü—É –≤ —Å–≤–æ–π—Å—Ç–≤–æ ${this}.currentTable `;

			// –ï—Å–ª–∏ —É –Ω–∞—Å –Ω–µ—Ç –Ω–∏ –º–∞—Å—Å–∏–≤–∞ —Å –¥–∞–Ω–Ω—ã–º–∏, –Ω–∏ –∫–ª—é—á–∞ localStorage -- –≤—ã–±—Ä–æ—Å –∏—Å–∫–ª—é—á–µ–Ω–∏—è
			}else if (this.localStorageKey == false && this.tableData == false) {
				
				throw `–ù–µ –Ω–∞–π–¥–µ–Ω—ã –¥–∞–Ω–Ω—ã–µ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è HTML-—Ç–∞–±–ª–∏—Ü—ã. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –¥–æ–±–∞–≤—Ç–µ –¥–∞–Ω–Ω—ã–µ. –ï—Å–ª–∏ —É –≤–∞—Å –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ -- –¥–æ–±–∞–≤—å—Ç–µ —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –≤ —Å–≤–æ–π—Å—Ç–≤–æ  ${this}.tableData, –µ—Å–ª–∏ —É –≤–∞—Å –¥–∞–Ω–Ω—ã–µ –≤ LocalStorage -- –¥–æ–±–∞–≤—Ç–µ –∫–ª—é—á –∫ –¥–∞–Ω–Ω—ã–º –≤ —Å–≤–æ–π—Å—Ç–≤–æ ${this}.localStorageKey`;
			}
		}
	}
// ---------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –ø–æ–∑–≤–æ–ª—è–µ—Ç –ø–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ —Å LocalStorage –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Ç–∞–±–ª–∏—Ü—ã.  
	 * @return {Object} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ã–º –º–æ–∂–Ω–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å —Ç–∞–±–ª–∏—Ü—É, –∏–ª–∏ false, –µ—Å–ª–∏ –Ω–µ —Å–º–æ–≥ –Ω–∞–π—Ç–∏ –µ–≥–æ –≤ localStorage.
	 */
	getTableData() { 

		let jsonObject = localStorage.getItem(this.localStorageKey);

		// –ï—Å–ª–∏ –µ—Å—Ç—å JSON-–¥–∞–Ω–Ω—ã–µ –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –∫–ª—é—á—É ‚Äî —Ç–æ–≥–¥–∞ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –ø–æ–¥–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ, –µ—Å–ª–∏ JSON –Ω–µ –Ω–∞–π–¥–µ–Ω ‚Äî –≤–æ–∑–≤—Ä–∞—â–∞–µ–º false.
		if (jsonObject) {

			// –ü–∞—Ä—Å–∏–º –¥–∞–Ω–Ω—ã–µ –∏–∑ localStorage. –ü–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ —Å –æ–±—ä–µ–∫—Ç–∞–º–∏
			jsonObject = JSON.parse(jsonObject);

			// –ï—Å–ª–∏ –ø–æ–ª–µ –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ "this.sortMark" —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É–µ—Ç —Å–≤–æ–π—Å—Ç–≤—É –≤ –æ–±—ä–µ–∫—Ç–µ
			if (jsonObject.some((elem)=>elem[this.sortMark])) {

				//–í–æ–∑–≤—Ä–∞—â–∞–µ–º –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –ø–æ –∑–∞—Ä–∞–Ω–µ–µ –∑–∞–¥–∞–Ω–Ω–æ–π —Ñ—É–Ω–∫—Ü–∏–∏
				return jsonObject.sort(this.sortFunc1.bind(this)) 

			// –ï—Å–ª–∏ –Ω–µ—Ç –∑–Ω–∞—á–µ–Ω–∏—è –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ "this.sortMark" ‚Äî –í–æ–∑–≤—Ä–∞—â–∞–µ–º –Ω–µ–æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤.
			}else if(this.sortMark == undefined){
				return jsonObject;

			// –ï—Å–ª–∏ –∑–Ω–∞—á–µ–Ω–∏—è –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ "this.sortMark" –Ω–µ –≤–µ—Ä–Ω–æ–µ ‚Äî –≤—ã–±—Ä–∞—Å—ã–≤–∞–µ–º –∏—Å–∫–ª—é—á–µ–Ω–∏–µ. 
			}else{
				throw "–ó–Ω–∞—á–µ–Ω–∏–µ, –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ ‚Äî –Ω–µ –≤–µ—Ä–Ω–æ–µ. –¢–∞–∫–æ–≥–æ —Å–≤–æ–π—Å—Ç–≤–∞ —É –æ–±—ä–µ–∫—Ç–∞ –Ω–µ—Ç."
			}

		}else {
			return false;
		}
	}

// ---------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –¥–æ–±–∞–≤–ª—è–µ—Ç —Ä—è–¥—ã —Å —è—á–µ–π–∫–∞–º–∏ –≤ —Ç–∞–±–ª–∏—Ü—É
	 * @todo —Ä–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –º–µ—Ç–æ–¥ –±–µ–∑ –∑–∞–º—ã–∫–∞–Ω–∏—è (–±–µ–∑ –≤–Ω—É—Ç—Ä–µ–Ω–µ–π —Ñ—É–Ω–∫—Ü–∏–∏)
	 * @param {object} dataInRow –î–∞–Ω–Ω—ã–µ –¥–ª—è 1 —Å—Ç—Ä–æ–∫–∏ (1 –æ–±—ä–µ–∫—Ç)	 
	 * @param {Number} numberOfRows –ö–æ–ª-–≤–æ —Ä—è–¥–æ–≤ (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é = 1)
	 * @param {Number} numberOfCells –ö–æ–ª-–≤–æ —è—á–µ–µ–∫ –≤ —Ä—è–¥–µ (–ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –≤—ã—Å—á–∏—Ç—ã–≤–∞–µ—Ç—Å—è –ø–æ –∫–æ–ª-–≤—É —è—á–µ–µ–∫ –≤ –∑–∞–≥–æ–ª–æ–≤–∫–µ (thead))
	 * @return {arrayofRows} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å–æ–∑–¥–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ —Å—Ç—Ä–æ–∫ (–ª–∏–±–æ 1 —Å—Ç—Ä–æ–∫—É) —Å –¥–∞–Ω–Ω—ã–º–∏.
	 */
	addRows(dataInRow, numberOfRows = 1, numberOfCells) {
		
		let arrayofRows = [], // –ú–∞—Å—Å–∏–≤ –±—É–¥—É—â–∏—Ö —Å—Ç—Ä–æ–∫
			row; // –¢–µ–∫—É—â–∞—è —Å—Ç—Ä–æ–∫–∞

		// –≠—Ç–∞ —Ñ—É–Ω–∫—Ü–∏—è —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–æ –∏ –¥–æ–±–∞–≤–ª—è–µ—Ç —Ä—è–¥, —è—á–µ–π–∫–∏ –∏ —Ç–µ–∫—Å—Ç –≤ —Ç–∞–±–ª–∏—Ü—É. –ü—Ä–∏–Ω–∏–º–∞–µ—Ç –∫–æ–Ω—Ç–µ–∫—Å—Ç, –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —Å —Å–æ–∑–¥–∞–Ω–Ω—ã–º–∏ —Å—Ç—Ä–æ–∫–∞–º–∏
		function add(context) {
			
			// –¶–∏–∫–ª –¥–æ–±–∞–≤–ª—è–µ–∏ –ø–æ 1-–π —Å—Ç—Ä–æ–∫–µ —Ç–æ –∫–æ–ª-–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ –º—ã —É–∫–∞–∂–µ–º (numberOfRows)
			for (let i = 0; i < numberOfRows; i++) {

				row = document.createElement("tr")
				context.currentTable.tBodies[0].appendChild(row)

				// –¶–∏–∫–ª –¥–æ–±–∞–≤–ª—è–µ—Ç –ø–æ 1-–π —è—á–µ–π–∫–µ —Ç–æ –∫–æ–ª-–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ –º—ã —É–∫–∞–∂–µ–º (numberOfCells)
				for (let j = 0; j < numberOfCells; j++) {

					let cell = document.createElement("td")
					row.appendChild(cell)
				}

				arrayofRows.push(row);
				
				if (dataInRow) {
					context.addInfoInRow(row.rowIndex, dataInRow);
				}
			}
			return arrayofRows;
		}

		// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –∞—Ä–≥—É–º–µ–Ω—Ç–∞ "numberOfCells", –µ—Å–ª–∏ –µ–≥–æ –Ω–µ—Ç‚Äî –∑–∞ –∫–æ–ª–∏—á–µ—Å—Ç–∫–æ —è—á–µ–µ–∫ –≤—ã—Å—á–∏—Ç—ã–≤–∞–µ—Ç—Å—è –∏–∑ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —è—á–µ–µ–∫ –ø–µ—Ä–≤–æ–≥–æ —Ä—è–¥–∞ (–∏–∑ "thead");
		if (numberOfCells == undefined) {
			numberOfCells = this.currentTable.rows[0].cells.length;
		}

		// –≠—Ç–æ—Ç —Å–ø–æ—Å–æ–± –±—É–¥–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è —á–∞—â–µ –≤—Å–µ–≥–æ –∏ –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å 1 —Å–æ–∑–¥–∞–Ω–Ω—ã–π —Ä—è–¥ —Å –¥–∞–Ω–Ω—ã–º–∏.
		if (numberOfRows == 1) {

			arrayofRows = add(this) // ? return add(this)[0] -- –ø–æ–ø—Ä–æ–±–æ–≤–∞—Ç—å –≤–µ—Ä–Ω—É—Ç—å —ç—Ç–æ
			return arrayofRows[0];
		}
		return add(this);
	}

// ---------------------------------------------------------------------------
	/**
	 * –º–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –∑–∞–ø–æ–ª–Ω—è–µ—Ç —Ä—è–¥ —Ç–∞–±–ª–∏—Ü—ã. –ò—Å–ø–æ–ª—å–∑—É–µ—Ç –º–µ—Ç–æ–¥ "addInfoInCell", –∫–æ—Ç–æ—Ä—ã–π –∑–∞–ø–æ–ª–Ω—è–µ—Ç –∫–∞–∂–¥—É—é —è—á–µ–π–∫—É
	 * @param {Number} indexOfRow –ò–Ω–¥–µ–∫—Å —Ä—è–¥–∞ –≤ —Ç–∞–±–ª–∏—Ü–µ, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –∑–∞–ø–æ–ª–Ω—è—Ç—Å—è –¥–∞–Ω–Ω—ã–º–∏ 
	 * @param {Object} object –û–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Ä—è–¥–∞. 
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	*/
	addInfoInRow(indexOfRow, object) {

		for (let i = 0; i < this.currentTable.rows[indexOfRow].cells.length; i++) {
			this.addInfoInCell(object, indexOfRow, i)
		}
	}

// ---------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –∑–∞–ø–æ–ª–Ω—è–µ—Ç –æ–¥–Ω—É —É–∫–∞–∑–∞–Ω–Ω—É—é —è—á–µ–π–∫—É –≤ —É–∫–∞–∑–∞–Ω–Ω–æ–º —Ä—è–¥–µ —Ç–∞–±–ª–∏—Ü—ã. –ù—É–∂–µ–Ω –¥–ª—è —Ä–∞–±–æ—Ç—ã –º–µ—Ç–æ–¥–∞ addInfoInRow
	 * @param {Object} object –û–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —è—á–µ–π–∫–∏.
	 * @param {Number} indexOfRow –ò–Ω–¥–µ–∫—Å —Ä—è–¥–∞, –≤ –∫–æ—Ç–æ—Ä–æ–º –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –Ω—É–∂–Ω–∞—è –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —è—á–µ–π–∫–∞
	 * @param {Number} indexOfCell –ò–Ω–¥–µ–∫—Å —è—á–µ–π–∫–∏, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –∑–∞–ø–æ–ª–Ω—è—Ç—Å—è.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	addInfoInCell(object, indexOfRow, indexOfCell) {

		for (const key in object) {
			if (object.hasOwnProperty(key)) {

				// –ï—Å–ª–∏ –∫–ª—é—á –æ–±—ä–µ–∫—Ç–∞ —Ä–∞–≤–Ω—è–µ—Ç—Å—è —Ç–æ–º—É, —á—Ç–æ –≤–µ—Ä–Ω—É–ª –º–µ—Ç–æ—Ç "getTableColumnHead" ‚Äî –∑–Ω–∞—á–µ–Ω–∏–µ –∏–∑ –æ–±—ä–µ–∫—Ç–∞ –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤ –∑–∞—Ä–∞–Ω–µ–µ –∑–∞–¥–∞–Ω–Ω—É—é —è—á–µ–π–∫—É
				if (key.toLowerCase() === this.getTableColumnHead(indexOfCell).toLowerCase()){

					this.currentTable.rows[indexOfRow].cells[indexOfCell].innerHTML = object[key];

					// –í–æ–∑–≤—Ä–∞—â–∞–µ–º –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω—É—é —è—á–µ–π–∫—É
					return this.currentTable.rows[indexOfRow].cells[indexOfCell]
				}
			}
		}
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –≤—ã—á–∏—Å–ª—è–µ—Ç, –∫–∞–∫–æ–π –∑–∞–≥–æ–ª–æ–≤–æ–∫ –Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è –Ω–∞–¥ —É–∫–∞–∑–∞–Ω–Ω–æ–π —è—á–µ–π–∫–æ–π (–ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–æ–ª–æ–Ω–∫–∏). –ù—É–∂–µ–Ω –¥–ª—è —Ä–∞–±–æ—Ç—ã –º–µ—Ç–æ–¥–∞ "addInfoInCell"
	 * @param {Number} indexOfCell –ò–Ω–¥–µ–∫—Å —è—á–µ–π–∫–∏, —É –∫–æ—Ç–æ—Ä–æ–π –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —É–∑–Ω–∞—Ç—å –∑–∞–≥–æ–ª–æ–≤–æ–∫.
	 * @return {String} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ –∑–∞–≥–æ–ª–æ–≤–∫–∞ –Ω–∞–¥ —è—á–µ–π–∫–æ–π.
	 * @see addInfoInCell
	 */
	getTableColumnHead(indexOfCell) {

		// –ü–æ–ª—É—á–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∏–∑ –∞—Ç—Ä–∏–±—É—Ç–∞ "data-object-key-bind" –≤ –∑–∞–≥–æ–ª–æ–≤–æ—á–Ω–æ–π —è—á–µ–π–∫–∏
		let nameOfTableHead = this.currentTable.rows[0].cells[indexOfCell].dataset.objectKeyBind;
		return nameOfTableHead;
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –ø–æ–ª–Ω–æ—Å—Ç—å—é –æ—á–∏—â–∞–µ—Ç –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã–π —Ä—è–¥ –ª–∏–±–æ —Ç–∞–±–ª–∏—Ü—É —Ü–µ–ª–∏–∫–æ–º
	 * @param {number} indexOfRow –ò–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏, –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ —É–¥–∞–ª–∏—Ç—å. –ï—Å–ª–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä –æ—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç --—Ç–∞–±–ª–∏—Ü–∞ –æ—á–∏—Å—Ç–∏—Ç—Å—è –ø–æ–ª–Ω–æ—Å—Ç—å—é.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	cleanTable(indexOfRow){

		indexOfRow = parseInt(indexOfRow);

		if (indexOfRow) {
			// –ü—Ä–æ–≤–µ—Ä–∫–∞, –ø—Ä–∞–≤–∏–ª—å–Ω–æ –ª–∏ –≤–≤–µ–¥–µ–Ω –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏
			if (indexOfRow <= this.currentTable.rows.length -1  && indexOfRow >= 0) {

				// –£–¥–∞–ª—è–µ–º —Å—Ç—Ä–æ–∫—É –ø–æ –∏–Ω–¥–µ–∫—Å—É "indexOfRow"
				this.currentTable.deleteRow(indexOfRow)
			}else{
				throw "–ù–µ–≤–æ–∑–º–æ–∂–Ω–æ —É–¥–∞–ª–∏—Ç—å —Å—Ç—Ä–æ–∫—É. –¢–∞–∫–æ–π —Å—Ç—Ä–æ–∫–∏ –Ω–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç. –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç—å –ø–µ—Ä–µ–¥–∞–≤–∞–µ–º–æ–≥–æ –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä –∑–Ω–∞—á–µ–Ω–∏—è."
			}
		}else{
			// –ó–∞–ø—É—Å–∫–∞–µ–º —Ü–∏–∫–ª –Ω–∞ –∏—Ç—Ç–µ—Ä–∞—Ü–∏–π —Ä–∞–≤–Ω—É—é –∫–æ–ª-–≤–æ —Å—Ç—Ä–æ–∫ –≤ —Ç–∞–±–ª–∏—Ü–µ. –ö–∞–∂–¥—ã–π —Ä–∞–∑ —É–¥–∞–ª—è–µ–º –ø–æ—Å–ª–µ–¥–Ω—é—é —Å—Ç—Ä–æ–∫—É.
			while (this.currentTable.rows.length > 1) {
				this.currentTable.deleteRow(this.currentTable.rows.length - 1);
			}
		}
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –ø–æ–∏—Å–∫–∞. –§–∏–ª—å—Ç—Ä—É–µ—Ç –º–∞—Å—Å–∏–≤ –ø–æ –∑–Ω–∞—á–µ–Ω–∏—é input.value, –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –µ–≥–æ.
	 * @param {object} input –≠–ª–µ–º–µ–Ω—Ç, –≤ –∫–æ—Ç–æ—Ä—ã–π –≤–≤–æ–¥–∏—Ç—Å—è —Ñ–∏–ª—å—Ç—Ä–∏—Ä—É—é—â–µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ.
	 * @param {string} objectProperty –ò–º—è —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥–µ—Ç –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç—å —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—è.
	 * @return {Array} –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ—Ç—Ñ–∏–ª—å—Ç–Ω–æ–≤–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –ª–∏–±–æ false, –µ—Å–ª–∏ –º–∞—Å—Å–∏–≤ –Ω–µ —É–¥–∞–ª–æ—Å—å –Ω–∞–π—Ç–∏.
	 */
	search(input, objectProperty){
		let tempVariable;
		this.tableData = this.getTableData();

		if(this.tableData){

			tempVariable = this.tableData.filter(element => {

				// –ü–µ—Ä–µ–≤–æ–¥–∏–º –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞ –≤ –Ω–∏–∂–Ω–∏–π —Ä–µ–≥–∏—Å—Ç—Ä 
				let tempProperty = element[objectProperty].trim().toLowerCase();
				// –ï—Å–ª–∏ "tempProperty" —Å–æ–¥–µ—Ä–∂–∏—Ç —Ç–æ, —á—Ç–æ –º—ã –≤–≤–µ–ª–∏ –≤ "input" -- –∑–Ω–∞—á–∏—Ç —ç—Ç–æ –∏—Å–∫–æ–º–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ.
				return tempProperty.includes(input.value.trim().toLowerCase());
			});
			return tempVariable;
			  
		}else{
			console.log ("–ù–µ —É–¥–∞–ª–æ—Å—å –Ω–∞–π—Ç–∏ –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏")
			return false;
		}
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –∑–∞–ø—É—Å–∫–∞–µ—Ç –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –ø–æ –∫–ª–∏–∫—É –Ω–∞ —Å—Ç—Ä–æ–∫—É. –ú–æ–∂–Ω–æ –ø–æ–¥–≤–µ—Å–∏—Ç—å —Å–≤–æ–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ (callbackHandler)
	 * @param {Function} callbackHandler –§—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è —Å—Ç–∞–≤–∏—Ç—Å—è –≤ –∫–∞—á–µ—Å—Ç–≤–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ –Ω–∞ –Ω–∞–∂–∞—Ç–∏–µ –ø–æ —Ç–∞–±–ª–∏—Ü–µ. (–ü–æ —É–º–æ–ª—á–∞–Ω–∏—é this.rowSelectHandler)
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	tableClickHandler(callbackHandler){

		this.otherRowSelectHandler = callbackHandler;

		if (this.otherRowSelectHandler) {
			this.currentTable.onclick = this.otherRowSelectHandler.bind(this);
		}else{
			this.currentTable.onclick = this.rowSelectHandler.bind(this);
		}
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –∫–ª–∏–∫ –ø–æ —Å—Ç—Ä–æ–∫–µ. –ï–≥–æ –∑–∞–¥–∞—á–∞ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å —Ç–∞–±–ª–∏—Ü—É, –µ—Å–ª–∏ –∫–ª–∏–∫ –±—ã–ª –ø–æ –∑–∞–≥–æ–ª–æ–≤–∫—É, –∏–ª–∏ –≤–µ—Ä–Ω—É—Ç—å —Å—Ç—Ä–æ–∫—É, –µ—Å–ª–∏ –∫–ª–∏–∫ –±—ã–ª –ø–æ "–Ω–µ –∑–∞–≥–æ–ª–æ–≤–æ—á–Ω–æ–π" —Å—Ç—Ä–æ–∫–µ.
	 * @todo –ù–æ–∂–Ω–∞ –ª–∏ —Ç—É—Ç –ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è "returnedValue" ?
	 * @param {object} eventObject –û–±—ä–µ–∫—Ç —Å–æ–±—ã—Ç–∏—è, –∫–æ—Ç–æ—Ä—ã–π –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è –ø—Ä–∏ –∫–ª–∏–∫–µ.
	 * @return {object} –í–æ–∑–≤—Ä–∞—â—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –ø–æ—Å–ª–µ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏, –ª–∏–±–æ –æ–±—ä–µ–∫—Ç, –Ω–∞ –∫–æ—Ç–æ—Ä—ã–π –∫–ª–∏–∫–Ω—É–ª –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å.
	 */
	rowSelectHandler(eventObject){

		let returnedValue; // –ó–Ω–∞—á–µ–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —ç—Ç–æ—Ç –º–µ—Ç–æ–¥.

		
		this.sortMark = eventObject.target.dataset.objectKeyBind; // –ó–∞–ø–∏—Å—ã–≤–∞–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
			
		// –ü—Ä–æ–≤–µ—Ä–∫–∞, –µ—Å–ª–∏ –µ—Å—Ç—å –∞—Ç—Ä–∏–±—É—Ç "objectKeyBind" —É HTML —ç–ª–µ–º–µ–Ω—Ç–∞ -- —Å–æ—Ä—Ç–∏—Ä—É–µ–º —Ç–∞–±–ª–∏—Ü—É
		if (this.sortMark && eventObject.target.tagName == "TH") {

			this.sortDirection = !this.sortDirection;
			
			// –°–æ—Ä—Ç–∏—Ä—É–µ–º —Ç–µ–∫—É—â–∏–µ –¥–∞–Ω–Ω—ã–µ. (–£—á–∏—Ç—ã–≤–∞—è –ø–æ—Ç–µ—Ä—é –∫–æ–Ω—Ç–µ–∫—Å—Ç–∞)
			this.tableData.sort(this.sortFunc1.bind(this));

			// –û—á–∏—â–∞–µ–º –≤—Å—é —Ç–∞–±–ª–∏—Ü—É
			this.cleanTable();

			// –ó–∞–ø–æ–ª–Ω—è–µ –Ω–æ–≤—ã–º–∏ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ (—Å —É—á–µ—Ç–æ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏)
			returnedValue = this.fillTable();
			this.tableData = returnedValue;

			this.selectedRowIndex = null;
			this.selectedObject = null;

		// –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∫–ª–∏–∫–Ω—É–ª –ø–æ —Å—Ç—Ä–æ–∫–µ —Å –¥–∞–Ω–Ω—ã–º–∏ -- –Ω–∞—Ö–æ–¥–∏–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –æ–±—ä–µ–∫—Ç —Å –º–∞—Å—Å–∏–≤–∞ –æ–±—ä–µ–∫—Ç–æ–≤ (–ø–æ —ç—Ç–æ–º—É –∏–Ω–¥–µ–∫—Å—É)
		}else if(eventObject.target.tagName == "TD"){

			this.selectedRowIndex = eventObject.target.parentElement.rowIndex; // –ó–∞–ø–∏—Å—ã–≤–∞–µ–º –∏–Ω–¥–µ–∫—Å –≤—ã–¥–µ–ª–µ–Ω–Ω–æ–π —Å—Ç—Ä–æ–∫–∏

			returnedValue = this.tableData[this.selectedRowIndex -1];
			this.selectedObject = returnedValue;
			this.formObject.fillForm(this.selectedObject)
			this.setClassToElement(this.currentTable.rows[this.selectedRowIndex], "row-selected")
			
			console.log(`selectedObject =  `, this.selectedObject);
			console.dir(`this.selectedRowIndex = ${this.selectedRowIndex} `);
		}

		// –ø–æ –æ–∫–æ–Ω—á–∞–Ω–∏—é –º–µ—Ç–æ–¥–∞ –∑–∞–ø—É—Å–∫–∞–µ–º –º–µ—Ç–æ–¥ –ø–∞—Ç—Ç–µ—Ä–Ω–∞ "observer" -- notify() —á—Ç–æ–±—ã –æ–±–Ω–æ–≤–∏—Ç—å –≤—Å–µ –¥–∞–Ω–Ω—ã–µ.
		this.notify();

		return returnedValue;
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–æ—Ö–æ–¥–∏—Ç—Å—è –ø–æ —Ç–∞–±–ª–∏—Ü–µ –∏ –ø—Ä–∏–º–µ–Ω—è–µ—Ç —Å—Ç–∏–ª—å –∫ 1 –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–º—É —Ä—è–¥—É (—Å—Ç—Ä–æ–∫–µ).
	 * @todo –ö–∞–∫ —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø—Ä–∞–≤–∏–ª—å–Ω–æ –∫–ª–∞—Å—Å —ç–ª–µ–º–µ–Ω—Ç—É
	 * @param {object} row –†—è–¥ –∫ –∫–æ—Ç–æ—Ä–æ–º—É –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø—Ä–∏–º–µ–Ω–∏—Ç—å –∫–ª–∞—Å
	 * @param {string} applying–°lass css –∫–ª–∞—Å, –∫–æ—Ç–æ—Ä—ã–π –Ω—É–∂–Ω–æ –ø—Ä–∏–º–µ–Ω–∏—Ç—å –∫ —Ä—è–¥—É.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	setClassToElement(row, applying–°lass){
		for (let i = 0; i < this.currentTable.rows.length ; i++) {
			this.currentTable.rows[i].className = "";
			if (this.currentTable.rows[i] == row) {
				this.currentTable.rows[i].className = applying–°lass;
			}
		}
	}

// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è –≤ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫—É –º–∞—Å—Å–∏–≤–∞. –°–æ—Ä—Ç–∏—Ä—É–µ—Ç –∑–Ω–∞—á–µ–Ω–∏—è –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é, –ª–∏–±–æ –ø–æ —É–±—ã–≤–∞–Ω–∏—é –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∑–Ω–∞—á–µ–Ω–∏—è "this.sortDirection".
	 * @param {object} a –ü–µ—Ä–≤—ã–π –ø–∞—Ä–∞–º–µ—Ç—Ä –¥–ª—è —Å—Ä–∞–≤–Ω–µ–Ω–∏—è —Å–æ –≤—Ç–æ—Ä—ã–º
	 * @param {object} b –í—Ç–æ—Ä–æ–π –ø–∞—Ä–∞–º–µ—Ç—Ä –¥–ª—è —Å—Ä–∞–≤–Ω–µ–Ω–∏—è —Å –ø–µ—Ä–≤—ã–º.
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	sortFunc1(a, b) { 
		
		if (this.sortDirection) {
			if (a[this.sortMark] >  b[this.sortMark]) return 1; // –µ—Å–ª–∏ –ø–µ—Ä–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –±–æ–ª—å—à–µ –≤—Ç–æ—Ä–æ–≥–æ
			if (a[this.sortMark] == b[this.sortMark]) return 0; // –µ—Å–ª–∏ —Ä–∞–≤–Ω—ã
			if (a[this.sortMark] <  b[this.sortMark]) return -1; // –µ—Å–ª–∏ –ø–µ—Ä–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –º–µ–Ω—å—à–µ –≤—Ç–æ—Ä–æ–≥–æ
		}else{
			if (a[this.sortMark] <  b[this.sortMark]) return 1; 
			if (a[this.sortMark] == b[this.sortMark]) return 0;
			if (a[this.sortMark] >  b[this.sortMark]) return -1;
		}	
	}
	
// -----------------------------------------------------------------------------
	/**
	 * –ú–µ—Ç–æ–¥ –æ–±–Ω—É–ª—è–µ—Ç –≤—ã–¥–µ–ª–µ–Ω–Ω—É—é —Å—Ç—Ä–æ–∫—É –≤ —Ç–∞–±–ª–∏—Ü–µ –∏ –≤ —Ñ–æ—Ä–º–µ
	 * @return {void} –ù–∏—á–µ–≥–æ –Ω–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç
	 */
	nullifySelection(){

		if (this.selectedRowIndex != null && this.currentTable.rows[this.selectedRowIndex]) {

			this.currentTable.rows[this.selectedRowIndex].classList.remove("row-selected");
		}

		this.formObject.selectedObject = null;
		this.selectedRowIndex = null;
		this.selectedObject = null;
	}
}